 #DE1E27;\n  --r-800: #D11320;\n  --r-900: #C30011;\n\n  --v-50: #FFFFFF;\n  --v-100: #F5EBFF;\n  --v-200: #DDB8FF;\n  --v-300: #C585FF;\n  --v-400: #AC52FF;\n  --v-500: #941FFF;\n  --v-600: #7B00EB;\n  --v-700: #6000B8;\n  --v-800: #450085;\n  --v-900: #2B0052;\n\n  // Common color aliases\n  --color-woot: var(--w-500);\n\n  --color-heading: #1f2d3d;\n  --color-body: #3c4858;\n\n  --color-border: var(--s-75);\n  --color-border-light: var(--s-50);\n  --color-border-dark: var(--s-100);\n\n  --color-background: var(--s-50);\n  --color-background-light: var(--s-25);\n\n  // Social and inboxes brand colors\n  --color-facebook-brand: #3b5998;\n  --color-twitter-brand: #1da1f2;\n  --color-whatsapp-brand: #25d366;\n  --color-sms-twilio: #f42f46;\n  --color-cloud-generic: #18b7b0;\n\n  // Tech-debt\n  --color-medium-gray: #8492a6;\n}\n",":root {\n  // spaces\n  --space-zero: 0;\n  --space-micro: 0.125rem;\n  --space-smaller: 0.25rem;\n  --space-small: 0.5rem;\n  --space-one: 0.625rem;\n  --space-slab: 0.75rem;\n  --space-snug: 0.875rem;\n  --space-normal: 1rem;\n  --space-two: 1.25rem;\n  --space-medium: 1.5rem;\n  --space-large: 2rem;\n  --space-larger: 3rem;\n  --space-jumbo: 4rem;\n  --space-mega: 6.25rem;\n  --space-giga: 15rem;\n\n  --space-minus-micro: -0.125rem;\n  --space-minus-smaller: -0.25rem;\n  --space-minus-half: -0.3125rem;\n  --space-minus-small: -0.5rem;\n  --space-minus-one: -0.625rem;\n  --space-minus-slab: -0.75rem;\n  --space-minus-snug: -0.875rem;\n  --space-minus-normal: -1rem;\n  --space-minus-two: -1.25rem;\n  --space-minus-medium: -1.5rem;\n  --space-minus-large: -2rem;\n  --space-minus-larger: -3rem;\n  --space-minus-jumbo: -4rem;\n  --space-minus-mega: -6.25rem;\n}\n",":root {\n  --font-size-nano: 0.5rem;\n  --font-size-micro: 0.625rem;\n  --font-size-mini: 0.75rem;\n  --font-size-small: 0.875rem;\n  --font-size-default: 1rem;\n  --font-size-medium: 1.125rem;\n  --font-size-two: 1.25rem;\n  --font-size-large: 1.375rem;\n  --font-size-big: 1.5rem;\n  --font-size-bigger: 1.875rem;\n  --font-size-mega: 2.125rem;\n  --font-size-giga: 2.5rem;\n}\n",":root {\n  --font-weight-feather: 100;\n  --font-weight-light: 300;\n  --font-weight-normal: 400;\n  --font-weight-medium: 500;\n  --font-weight-bold: 600;\n  --font-weight-black: 700;\n}\n",":root {\n    --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1),\n        0 1px 2px 0 rgba(0, 0, 0, 0.06);\n    --shadow-small: 0 1px 2px 0 rgba(0, 0, 0, 0.05);\n    --shadow-medium: 0 4px 6px -1px rgba(0, 0, 0, 0.1),\n        0 2px 4px -1px rgba(0, 0, 0, 0.06);\n    --shadow-large: 0 10px 15px -3px rgba(0, 0, 0, 0.1),\n        0 4px 6px -2px rgba(0, 0, 0, 0.05);\n    --shadow-larger: 0 20px 25px -5px rgba(0, 0, 0, 0.1),\n        0 10px 10px -5px rgba(0, 0, 0, 0.04);\n    --shadow-dropdown-pane:\n        0 0.5rem 1rem rgb(50 50 93 / 8%),\n        0 0.25rem 0.75rem rgb(0 0 0 / 7%);\n    --shadow-bulk-action-container:\n        6px 3px 22px 9px rgb(181 181 181 / 25%);\n    --shadow-context-menu: rgb(22 23 24 / 30%) 0px 10px 38px -10px,\n    rgb(22 23 24 / 20%) 0px 10px 20px -15px;\n    --shadow-widget-builder: 0 0px 20px 5px rgb(0 0 0 / 10%);\n}\n",":root {\n  --border-radius-small: 0.1875rem;\n  --border-radius-normal: 0.3125rem;\n  --border-radius-medium: 0.4375rem;\n  --border-radius-large: 0.5625rem;\n  --border-radius-full: 6.25rem;\n  --border-radius-rounded: 50%;\n}\n",":root {\n  // z-index\n  --z-index-minus: -1;\n  --z-index-zero: 0;\n  --z-index-one: 1;\n  --z-index-low: 10;\n  --z-index-twenty: 20;\n  --z-index-normal: 100;\n  --z-index-high: 1000;\n  --z-index-higher: 2000;\n  --z-index-much-higher: 5000;\n  --z-index-very-high: 9999;\n  --z-index-highest: 10000;\n}\n","// Font sizes\n$font-size-nano: 0.5rem;\n$font-size-micro: 0.675rem;\n$font-size-mini: 0.75rem;\n$font-size-small: 0.875rem;\n$font-size-default: 1rem;\n$font-size-medium: 1.125rem;\n$font-size-large: 1.375rem;\n$font-size-big: 1.5rem;\n$font-size-bigger: 1.75rem;\n$font-size-mega: 2.125rem;\n$font-size-giga: 2.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.125rem;\n$space-smaller: 0.25rem;\n$space-small: 0.5rem;\n$space-one: 0.675rem;\n$space-slab: 0.75rem;\n$space-normal: 1rem;\n$space-two: 1.25rem;\n$space-medium: 1.5rem;\n$space-large: 2rem;\n$space-larger: 3rem;\n$space-jumbo: 4rem;\n$space-mega: 6.25rem;\n\n// font-weight\n$font-weight-feather: 100;\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-medium: 500;\n$font-weight-bold: 600;\n$font-weight-black: 700;\n\n//Navbar\n$nav-bar-width: 14.375rem;\n$header-height: 3.5rem;\n\n$woot-logo-padding: $space-large $space-two;\n\n// Colors\n$color-woot: #1f93ff;\n$color-gray: #6e6f73;\n$color-light-gray: #999a9b;\n\n$color-border: var(--s-75);\n$color-border-light: var(--s-50);\n$color-border-dark: var(--s-100);\n\n$color-background: var(--s-50);\n$color-background-light: var(--s-25);\n\n$color-white: #fff;\n$color-body: #3c4858;\n$color-heading: #1f2d3d;\n$color-extra-light-blue: #f5f7f9;\n\n$primary-color: $color-woot;\n$secondary-color: #5d7592;\n$success-color: #44ce4b;\n$warning-color: #ffc532;\n$alert-color: #ff382d;\n\n$masked-bg: rgba(0, 0, 0, .4);\n\n// Color-palettes\n\n$color-primary-light: #c7e3ff;\n$color-primary-dark: darken($color-woot, 20%);\n\n// Thumbnail\n$thumbnail-radius: 2.5rem;\n\n// chat-header\n$conv-header-height: 2.5rem;\n\n// Inbox List\n\n$inbox-thumb-size: 3rem;\n\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: .4s !default;\n$swift-ease-out-function: cubic-bezier(0.37, 0, 0.63, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-function !default;\n\n// Ionicons\n$ionicons-font-path: '~ionicons/fonts';\n\n// Transitions\n$transition-ease-in: all 0.250s ease-in;\n\n:root {\n  --dashboard-app-tabs-height: 2.4375rem;\n}\n","// loader class\n.spinner {\n  @include color-spinner();\n  @apply inline-block h-6 py-0 px-6 relative align-middle w-6;\n\n  &.message {\n    @include normal-shadow;\n    @apply bg-white dark:bg-slate-800 rounded-full left-0 my-3 mx-auto p-4 top-0;\n\n    &::before {\n      @apply -ml-3 -mt-3;\n    }\n  }\n\n  &.small {\n    @apply h-4 w-4;\n\n    &::before {\n      @apply h-4 -mt-2 w-4;\n    }\n  }\n}\n","@import '~dashboard/assets/scss/variables';\n@import '~widget/assets/scss/mixins';\n\n$spinner-before-border-color: rgba(255, 255, 255, 0.7);\n\n//borders\n@mixin border-nil() {\n  border-color: transparent;\n  border: 0;\n}\n\n@mixin thin-border($color) {\n  border: 1px solid $color;\n}\n\n@mixin custom-border-bottom($size, $color) {\n  border-bottom: $size solid $color;\n}\n\n@mixin custom-border-top($size, $color) {\n  border-top: $size solid $color;\n}\n\n@mixin border-normal() {\n  @apply border border-slate-50 dark:border-slate-700;\n}\n\n@mixin border-normal-left() {\n  @apply border-l border-slate-50 dark:border-slate-700;\n}\n\n@mixin border-normal-top() {\n  @apply border-t border-slate-50 dark:border-slate-700;\n}\n\n@mixin border-normal-right() {\n  @apply border-r border-slate-50 dark:border-slate-700;\n}\n\n@mixin border-normal-bottom() {\n  @apply border-b border-slate-50 dark:border-slate-700;\n}\n\n@mixin border-light() {\n  @apply border border-slate-25 dark:border-slate-700;\n}\n\n@mixin border-light-left() {\n  @apply border-l border-slate-25 dark:border-slate-700;\n}\n\n@mixin border-light-top() {\n  @apply border-t border-slate-25 dark:border-slate-700;\n}\n\n@mixin border-light-right() {\n  @apply border-r border-slate-25 dark:border-slate-700;\n}\n\n@mixin border-light-bottom() {\n  @apply border-b border-slate-25 dark:border-slate-700;\n}\n\n// background\n@mixin background-gray() {\n  background: $color-background;\n}\n\n@mixin background-light() {\n  @apply bg-slate-50 dark:bg-slate-800;\n}\n\n@mixin background-white() {\n  @apply bg-white dark:bg-slate-900;\n}\n\n// input form\n@mixin ghost-input() {\n  box-shadow: none;\n  border-color: transparent;\n\n  &:active,\n  &:hover,\n  &:focus {\n    border-color: transparent;\n    box-shadow: none;\n  }\n}\n\n// flex-layout\n@mixin space-between() {\n  display: flex;\n  justify-content: space-between;\n}\n\n@mixin space-between-column() {\n  @include space-between;\n  flex-direction: column;\n}\n\n@mixin space-between-row() {\n  @include space-between;\n  flex-direction: row;\n}\n\n@mixin flex-shrink() {\n  flex: 0 0 auto;\n  max-width: 100%;\n}\n\n@mixin flex-weight($value) {\n  // Grab flex-grow for older browsers.\n  $flex-grow: nth($value, 1);\n\n  // 2009\n  @include prefixer(box-flex, $flex-grow, webkit moz spec);\n\n  // 2011 (IE 10), 2012\n  @include prefixer(flex, $value, webkit moz ms spec);\n}\n\n// full height\n@mixin full-height() {\n  height: 100%;\n}\n\n@mixin round-corner() {\n  border-radius: 1000px;\n}\n\n@mixin scroll-on-hover() {\n  overflow: hidden;\n\n  &:hover {\n    overflow-y: auto;\n  }\n}\n\n\n@mixin horizontal-scroll() {\n  overflow-y: auto;\n}\n\n@mixin elegant-card() {\n  @include normal-shadow;\n  border-radius: $space-small;\n}\n\n@mixin color-spinner() {\n  @keyframes spinner {\n    to {\n      transform: rotate(360deg);\n    }\n  }\n\n  &::before {\n    animation: spinner .9s linear infinite;\n    border: 2px solid $spinner-before-border-color;\n    border-radius: 50%;\n    border-top-color: lighten($color-woot, 10%);\n    box-sizing: border-box;\n    content: '';\n    height: $space-medium;\n    left: 50%;\n    margin-left: -$space-one;\n    margin-top: -$space-one;\n    position: absolute;\n    top: 50%;\n    width: $space-medium;\n  }\n}\n\n// --------------------------------------------------------\n// arrows\n// --------------------------------------------------------\n// $direction: top, left, right, bottom, top-left, top-right, bottom-left, bottom-right\n// $color: hex, rgb or rbga\n// $size: px or em\n// @example\n// .element{\n//     @include arrow(top, #000, 50px);\n// }\n@mixin arrow($direction, $color, $size) {\n  display: block;\n  height: 0;\n  width: 0;\n  content: '';\n\n  @if $direction == 'top' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'right' {\n    border-bottom: $size solid transparent;\n    border-left: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom' {\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'left' {\n    border-bottom: $size solid transparent;\n    border-right: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'top-left' {\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'top-right' {\n    border-left: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'bottom-left' {\n    border-bottom: $size solid $color;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom-right' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n  }\n}\n\n@mixin text-ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n@mixin three-column-grid($column-one-width: 16rem,\n  $column-three-width: 16rem) {\n  width: 100%;\n  height: 100%;\n  display: grid;\n  grid-template-columns: minmax($column-one-width, 6fr) 10fr minmax($column-three-width, 6fr);\n}\n","// scss-lint:disable PseudoElement SpaceBeforeBrace VendorPrefix\n$shadow-color-1: rgba(50, 50, 93, 0.08);\n$shadow-color-2: rgba(0, 0, 0, 0.07);\n$shadow-color-3: rgba(50, 50, 93, 0.08);\n$shadow-color-4: rgba(0, 0, 0, 0.05);\n\n$color-shadow-medium: rgba(50, 50, 93, 0.08);\n$color-shadow-light: rgba(50, 50, 93, 0.04);\n$color-shadow-large: rgba(50, 50, 93, 0.25);\n$color-shadow-outline: rgba(66, 153, 225, 0.5);\n\n@mixin normal-shadow {\n  box-shadow: 0 $space-small $space-normal $shadow-color-1,\n    0 $space-smaller $space-slab $shadow-color-2;\n}\n\n@mixin light-shadow {\n  box-shadow: 0 $space-smaller 6px $shadow-color-3, 0 1px 3px $shadow-color-4;\n}\n\n@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content;\n  }\n\n  &:-moz-placeholder {\n    @content;\n  }\n\n  &::-moz-placeholder {\n    @content;\n  }\n\n  &:-ms-input-placeholder {\n    @content;\n  }\n}\n\n@mixin shadow {\n  box-shadow: 0 1px 10px 2px $color-shadow-medium,\n    0 1px 5px 1px $color-shadow-light;\n}\n\n@mixin shadow-medium {\n  box-shadow: 0 4px 24px 4px $color-shadow-medium,\n    0 2px 16px 2px $color-shadow-light;\n}\n\n@mixin shadow-large {\n  box-shadow: 0 10px 15px -16px $color-shadow-medium,\n    0 4px 6px -8px $color-shadow-light;\n}\n\n@mixin shadow-big {\n  box-shadow: 0 20px 25px -20px $color-shadow-medium,\n    0 10px 10px -10px $color-shadow-light;\n}\n\n@mixin shadow-mega {\n  box-shadow: 0 25px 50px -12px $color-shadow-big;\n}\n\n@mixin shadow-inner {\n  box-shadow: inset 0 2px 4px 0 $color-shadow-light;\n}\n\n@mixin shadow-outline {\n  box-shadow: 0 0 0 3px $color-shadow-outline;\n}\n\n@mixin shadow-none {\n  box-shadow: none;\n}\n\n@mixin button-size {\n  min-height: $space-large;\n  min-width: $space-large;\n}\n","@import '~dashboard/assets/scss/variables';\n\n.formulate-input {\n  .formulate-input-errors {\n    list-style-type: none;\n    margin: 0;\n    padding: 0;\n  }\n\n  .formulate-input-error {\n    color: var(--r-400);\n    display: block;\n    font-size: var(--font-size-small);\n    font-weight: $font-weight-normal;\n    margin-bottom: $space-one;\n    width: 100%;\n  }\n}\n\n.integration-hooks {\n  .formulate-input[data-type='checkbox'] {\n    .formulate-input-wrapper {\n      @apply flex;\n\n      .formulate-input-element {\n        @apply pr-2;\n\n        input {\n          @apply mb-0;\n        }\n      }\n    }\n\n    .formulate-input-element-decorator {\n      @apply hidden;\n    }\n  }\n}\n","@import './var.scss';\n\n.#{$namespace}-icon-left:before,\n.#{$namespace}-icon-right:before,\n.#{$namespace}-icon-double-left:before,\n.#{$namespace}-icon-double-right:before,\n.#{$namespace}-icon-double-left:after,\n.#{$namespace}-icon-double-right:after {\n  content: '';\n  position: relative;\n  top: -1px;\n  display: inline-block;\n  width: 10px;\n  height: 10px;\n  vertical-align: middle;\n  border-style: solid;\n  border-color: currentColor;\n  border-width: 2px 0 0 2px;\n  border-radius: 1px;\n  box-sizing: border-box;\n  transform-origin: center;\n  transform: rotate(-45deg) scale(0.7);\n}\n\n.#{$namespace}-icon-double-left:after {\n  left: -4px;\n}\n\n.#{$namespace}-icon-double-right:before {\n  left: 4px;\n}\n\n.#{$namespace}-icon-right:before,\n.#{$namespace}-icon-double-right:before,\n.#{$namespace}-icon-double-right:after {\n  transform: rotate(135deg) scale(0.7);\n}\n","@import './var.scss';\n\n.#{$namespace}-btn {\n  box-sizing: border-box;\n  line-height: 1;\n  font-size: 14px;\n  font-weight: 500;\n  padding: 7px 15px;\n  margin: 0;\n  cursor: pointer;\n  background-color: transparent;\n  outline: none;\n  border: 1px solid rgba(0, 0, 0, 0.1);\n  border-radius: 4px;\n  color: $default-color;\n  white-space: nowrap;\n  &:hover {\n    border-color: $primary-color;\n    color: $primary-color;\n  }\n}\n\n.#{$namespace}-btn-text {\n  border: 0;\n  padding: 0 4px;\n  text-align: left;\n  line-height: inherit;\n}\n","$namespace: 'mx' !default;\n\n$default-color: #73879c !default;\n$primary-color: #1284e7 !default;\n\n$today-color: mix(#fff, $primary-color, 10%) !default;\n\n$popup-z-index: 2001 !default;\n\n$input-border-color: #ccc !default;\n$input-color: #555 !default;\n$input-hover-border-color: #409aff !default;\n\n$disabled-color: #ccc !default;\n$disabled-background-color: #f3f3f3 !default;\n\n$border-color: #e8e8e8 !default;\n\n$calendar-active-color: #fff !default;\n$calendar-active-background-color: $primary-color !default;\n\n$calendar-hover-color: $default-color !default;\n$calendar-hover-background-color: mix(#fff, $calendar-active-background-color, 95%) !default;\n\n$calendar-in-range-color: $default-color !default;\n$calendar-in-range-background-color: mix(#fff, $calendar-active-background-color, 85%) !default;\n\n$time-active-color: $primary-color !default;\n$time-active-background-color: transparent !default;\n\n$time-hover-color: $default-color !default;\n$time-hover-background-color: mix(#fff, $calendar-active-background-color, 95%) !default;\n\n$input-border-radius: 4px !default;\n$sidebar-margin-left: 100px !default;\n","@import './var.scss';\n\n.#{$namespace}-scrollbar {\n  height: 100%;\n  &:hover {\n    .#{$namespace}-scrollbar-track {\n      opacity: 1;\n    }\n  }\n}\n\n.#{$namespace}-scrollbar-wrap {\n  height: 100%;\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n.#{$namespace}-scrollbar-track {\n  position: absolute;\n  top: 2px;\n  right: 2px;\n  bottom: 2px;\n  width: 6px;\n  z-index: 1;\n  border-radius: 4px;\n  opacity: 0;\n  transition: opacity 0.24s ease-out;\n  .#{$namespace}-scrollbar-thumb {\n    position: absolute;\n    width: 100%;\n    height: 0;\n    cursor: pointer;\n    border-radius: inherit;\n    background-color: rgba(144, 147, 153, 0.3);\n    transition: background-color 0.3s;\n  }\n}\n","@import './var.scss';\n\n.#{$namespace}-zoom-in-down-enter-active,\n.#{$namespace}-zoom-in-down-leave-active {\n  opacity: 1;\n  transform: scaleY(1);\n  transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1),\n    opacity 0.3s cubic-bezier(0.23, 1, 0.32, 1);\n  transform-origin: center top;\n}\n\n.#{$namespace}-zoom-in-down-enter,\n.#{$namespace}-zoom-in-down-leave-to {\n  opacity: 0;\n  transform: scaleY(0);\n}\n","@import './var.scss';\n@import './icon.scss';\n@import './btn.scss';\n@import './scrollbar.scss';\n@import './animation.scss';\n\n.#{$namespace}-datepicker {\n  position: relative;\n  display: inline-block;\n  width: 210px;\n  svg {\n    width: 1em;\n    height: 1em;\n    vertical-align: -0.15em;\n    fill: currentColor;\n    overflow: hidden;\n  }\n}\n\n.#{$namespace}-datepicker-range {\n  width: 320px;\n}\n\n.#{$namespace}-datepicker-inline {\n  width: auto;\n}\n\n.#{$namespace}-input-wrapper {\n  position: relative;\n  .#{$namespace}-icon-clear {\n    display: none;\n  }\n  &:hover {\n    .#{$namespace}-icon-clear {\n      display: block;\n    }\n    .#{$namespace}-icon-clear + .#{$namespace}-icon-calendar {\n      display: none;\n    }\n  }\n}\n\n.#{$namespace}-input {\n  display: inline-block;\n  box-sizing: border-box;\n  width: 100%;\n  height: 34px;\n  padding: 6px 30px;\n  padding-left: 10px;\n  font-size: 14px;\n  line-height: 1.4;\n  color: $input-color;\n  background-color: #fff;\n  border: 1px solid $input-border-color;\n  border-radius: $input-border-radius;\n  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);\n\n  &:hover,\n  &:focus {\n    border-color: $input-hover-border-color;\n  }\n  &:disabled,\n  &.disabled {\n    color: $disabled-color;\n    background-color: $disabled-background-color;\n    border-color: $input-border-color;\n    cursor: not-allowed;\n  }\n  &:focus {\n    outline: none;\n  }\n  &::-ms-clear {\n    display: none;\n  }\n}\n\n.#{$namespace}-icon-calendar,\n.#{$namespace}-icon-clear {\n  position: absolute;\n  top: 50%;\n  right: 8px;\n  transform: translateY(-50%);\n  font-size: 16px;\n  line-height: 1;\n  color: rgba(0, 0, 0, 0.5);\n  vertical-align: middle;\n}\n\n.#{$namespace}-icon-clear {\n  cursor: pointer;\n  &:hover {\n    color: rgba(0, 0, 0, 0.8);\n  }\n}\n\n.#{$namespace}-datepicker-main {\n  font: 14px/1.5 'Helvetica Neue', Helvetica, Arial, 'Microsoft Yahei', sans-serif;\n  color: $default-color;\n  background-color: #fff;\n  border: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-popup {\n  position: absolute;\n  margin-top: 1px;\n  margin-bottom: 1px;\n  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);\n  z-index: $popup-z-index;\n}\n\n.#{$namespace}-datepicker-sidebar {\n  float: left;\n  box-sizing: border-box;\n  width: $sidebar-margin-left;\n  padding: 6px;\n  overflow: auto;\n}\n\n.#{$namespace}-datepicker-sidebar + .#{$namespace}-datepicker-content {\n  margin-left: $sidebar-margin-left;\n  border-left: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-body {\n  position: relative;\n  user-select: none;\n}\n\n.#{$namespace}-btn-shortcut {\n  display: block;\n  padding: 0 6px;\n  line-height: 24px;\n}\n\n.#{$namespace}-range-wrapper {\n  display: flex;\n  @media (max-width: 750px) {\n    flex-direction: column;\n  }\n}\n\n.#{$namespace}-datepicker-header {\n  padding: 6px 8px;\n  border-bottom: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-footer {\n  padding: 6px 8px;\n  text-align: right;\n  border-top: 1px solid $border-color;\n}\n\n.#{$namespace}-calendar {\n  box-sizing: border-box;\n  width: 248px;\n  padding: 6px 12px;\n  & + & {\n    border-left: 1px solid $border-color;\n  }\n}\n\n.#{$namespace}-calendar-header {\n  box-sizing: border-box;\n  height: 34px;\n  line-height: 34px;\n  text-align: center;\n  overflow: hidden;\n}\n\n.#{$namespace}-btn-icon-left,\n.#{$namespace}-btn-icon-double-left {\n  float: left;\n}\n.#{$namespace}-btn-icon-right,\n.#{$namespace}-btn-icon-double-right {\n  float: right;\n}\n\n.#{$namespace}-calendar-header-label {\n  font-size: 14px;\n}\n\n.#{$namespace}-calendar-decade-separator {\n  margin: 0 2px;\n  &:after {\n    content: '~';\n  }\n}\n\n.#{$namespace}-calendar-content {\n  position: relative;\n  height: 224px;\n  box-sizing: border-box;\n  .cell {\n    cursor: pointer;\n    &:hover {\n      color: $calendar-hover-color;\n      background-color: $calendar-hover-background-color;\n    }\n    &.active {\n      color: $calendar-active-color;\n      background-color: $calendar-active-background-color;\n    }\n    &.in-range,\n    &.hover-in-range {\n      color: $calendar-in-range-color;\n      background-color: $calendar-in-range-background-color;\n    }\n    &.disabled {\n      cursor: not-allowed;\n      color: $disabled-color;\n      background-color: $disabled-background-color;\n    }\n  }\n}\n\n.#{$namespace}-calendar-week-mode {\n  .#{$namespace}-date-row {\n    cursor: pointer;\n    &:hover {\n      background-color: $calendar-hover-background-color;\n    }\n    &.#{$namespace}-active-week {\n      background-color: $calendar-in-range-background-color;\n    }\n    .cell {\n      &:hover {\n        color: inherit;\n        background-color: transparent;\n      }\n      &.active {\n        color: inherit;\n        background-color: transparent;\n      }\n    }\n  }\n}\n\n.#{$namespace}-week-number {\n  opacity: 0.5;\n}\n\n.#{$namespace}-table {\n  table-layout: fixed;\n  border-collapse: separate;\n  border-spacing: 0;\n  width: 100%;\n  height: 100%;\n  box-sizing: border-box;\n  text-align: center;\n\n  th {\n    padding: 0;\n    font-weight: 500;\n    vertical-align: middle;\n  }\n  td {\n    padding: 0;\n    vertical-align: middle;\n  }\n}\n\n.#{$namespace}-table-date {\n  td,\n  th {\n    height: 32px;\n    font-size: 12px;\n  }\n\n  .today {\n    color: $today-color;\n  }\n  .cell.not-current-month {\n    color: #ccc;\n    background: none; // cover the in-range style\n  }\n}\n\n.#{$namespace}-time {\n  flex: 1;\n  width: 224px;\n  background: #fff;\n  & + & {\n    border-left: 1px solid $border-color;\n  }\n}\n.#{$namespace}-calendar-time {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n.#{$namespace}-time-header {\n  @extend .#{$namespace}-calendar-header;\n  border-bottom: 1px solid $border-color;\n}\n\n.#{$namespace}-time-content {\n  height: 224px;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n.#{$namespace}-time-columns {\n  display: flex;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n\n.#{$namespace}-time-column {\n  flex: 1;\n  position: relative;\n  border-left: 1px solid $border-color;\n  text-align: center;\n\n  &:first-child {\n    border-left: 0;\n  }\n  .#{$namespace}-time-list {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n    &::after {\n      content: '';\n      display: block;\n      height: 32 * 6px;\n    }\n  }\n  .#{$namespace}-time-item {\n    cursor: pointer;\n    font-size: 12px;\n    height: 32px;\n    line-height: 32px;\n    &:hover {\n      color: $time-hover-color;\n      background-color: $time-hover-background-color;\n    }\n    &.active {\n      color: $time-active-color;\n      background-color: $time-active-background-color;\n      font-weight: 700;\n    }\n    &.disabled {\n      cursor: not-allowed;\n      color: $disabled-color;\n      background-color: $disabled-background-color;\n    }\n  }\n}\n\n.#{$namespace}-time-option {\n  cursor: pointer;\n  padding: 8px 10px;\n  font-size: 14px;\n  line-height: 20px;\n  &:hover {\n    color: $time-hover-color;\n    background-color: $time-hover-background-color;\n  }\n  &.active {\n    color: $time-active-color;\n    background-color: $time-active-background-color;\n    font-weight: 700;\n  }\n  &.disabled {\n    cursor: not-allowed;\n    color: $disabled-color;\n    background-color: $disabled-background-color;\n  }\n}\n","@import '~vue2-datepicker/scss/index';\n\n.date-picker {\n  // To be removed one SLA reports date picker is created\n  &.small {\n    .mx-input {\n      @apply h-8 text-sm;\n    }\n  }\n\n  &.no-margin {\n    .mx-input {\n      @apply mb-0;\n    }\n  }\n\n  &:not(.auto-width) {\n    .mx-datepicker-range {\n      @apply w-[320px];\n    }\n  }\n\n  .mx-datepicker {\n    @apply w-full;\n  }\n\n  .mx-input {\n    @apply h-[2.5rem] flex border border-solid border-slate-200 dark:border-slate-600 rounded-md shadow-none;\n  }\n\n  .mx-input:disabled,\n  .mx-input[readonly] {\n    @apply bg-white dark:bg-slate-900 cursor-pointer;\n  }\n\n  .mx-icon-calendar {\n    @apply dark:text-slate-500;\n  }\n}\n\n.mx-datepicker-main {\n  @apply border-0 bg-white dark:bg-slate-800;\n\n  .cell {\n    &.disabled {\n      @apply bg-slate-25 dark: